


































import Vue from 'vue';
import AppreciationCard from '@/data/ac';
import List from '@/components/List.vue';

import { acModule } from '@/store/modules/acs';

export default Vue.extend({
  name: 'Sent',
  components: {
    List
  },
  computed: {
    sentACs(): Array<AppreciationCard> {
      const acs: Array<AppreciationCard> = acModule.sentACs;
      return [...acs].sort((a, b) => b.dateSent - a.dateSent);
    }
  },
  methods: {
    timeString(timestamp: number): string {
      const d = new Date(timestamp);
      return d.toDateString();
    }
  }
});
